body {
  color: #151515;
  font-family: Poppins, sans-serif;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin-top: 0;
  margin-bottom: 0;
}

h2 {
  color: #151515;
  margin-bottom: 16px;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.5;
}

.container {
  width: 1260px;
  margin: 0 auto;
  padding: 0 45px;
}

.list-general {
  padding-left: 0;
  list-style: none;
}

.links-general {
  color: inherit;
  text-decoration: none;
}

.image-display {
  display: block;
}

.animate__heartBeat {
  animation-duration: 2s;
  animation-iteration-count: infinite;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.modal__close-btn {
  justify-content: center;
  align-items: center;
  display: flex;
}

.top-bar__wrapper {
  border-bottom: 1px solid rgba(20, 20, 20, .05);
  justify-content: space-between;
  padding-top: 16px;
  padding-bottom: 16px;
  display: flex;
}

.top-bar .contacts {
  column-gap: 38px;
  display: flex;
}

.top-bar .contacts__item {
  color: #151515;
  font-family: Open Sans;
  font-size: 12px;
  line-height: 1.33;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), text-shadow .25s cubic-bezier(.4, 0, .2, 1);
}

.top-bar .contacts__item:first-child {
  color: #6a983c;
}

.top-bar .contacts__link {
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
}

.top-bar .contacts__link:hover, .top-bar .contacts__link:focus {
  text-shadow: 0 1px 3px rgba(70, 118, 10, .12);
  transform: scale(1.3);
}

.top-bar .nav {
  column-gap: 38px;
  display: flex;
}

.top-bar .nav__item {
  color: #6a983c;
  font-family: Open Sans;
  font-size: 12px;
  line-height: 1.33;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), text-shadow .25s cubic-bezier(.4, 0, .2, 1);
}

.top-bar .nav__link {
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
}

.top-bar .nav__link:hover, .top-bar .nav__link:focus {
  text-shadow: 0 1px 3px rgba(70, 118, 10, .12);
  transform: scale(1.3);
}

.middle-bar__list {
  justify-content: space-between;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 40px;
  display: flex;
}

.middle-bar .logo {
  max-width: 177px;
  max-height: 50px;
  padding-top: 16px;
  padding-bottom: 16px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
}

.middle-bar .logo__icon {
  width: 177px;
  height: 18px;
  fill: #151515;
}

.middle-bar .logo:hover, .middle-bar .logo:focus {
  transform: scale(1.1);
}

.search__form {
  min-width: 500px;
  height: 42px;
  background: #f9f9f9;
  border: 1px solid #d1d1d1;
  border-radius: 12px;
  justify-content: space-between;
  align-items: center;
  padding: 10px 16px;
  display: flex;
}

.search__form--selected {
  border-bottom-left-radius: 0;
}

.search__input-wrapper {
  border-left: 1px solid #d1d1d1;
  justify-content: space-between;
  align-items: center;
  margin-left: 23px;
  padding-left: 23px;
  display: flex;
}

.search__input {
  width: 252px;
  color: #151515;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  font-family: Open Sans;
  font-size: 14px;
  line-height: 1.357;
}

.search__input::-ms-input-placeholder {
  color: #a9a9a9;
  font-family: Open Sans;
  font-size: 14px;
  line-height: 1.357;
}

.search__input::placeholder {
  color: #a9a9a9;
  font-family: Open Sans;
  font-size: 14px;
  line-height: 1.357;
}

.search__btn {
  width: 16px;
  height: 16px;
  box-sizing: content-box;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding-left: 0;
  padding-right: 0;
}

.search__icon {
  width: 16px;
  height: 16px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.search__btn:hover .search__icon, .search__btn:focus .search__icon {
  transform: scale(1.3);
}

.select {
  white-space: nowrap;
  position: relative;
}

.select__btn {
  min-width: 100%;
  text-align: left;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: flex-start;
  padding-left: 0;
  padding-right: 24px;
  font-size: 15px;
  font-weight: 700;
  line-height: 1.467;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
}

.select__icon {
  width: 16px;
  height: 16px;
  pointer-events: none;
  transition: transform .3s linear .1s;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.select__btn:hover, .select__btn:focus, .select__btn:active {
  transform: scale(1.1);
}

.select__btn:hover + .select__icon, .select__btn:focus + .select__icon, .select__btn:active + .select__icon {
  transform: translateY(-50%)rotate(90deg)translateY(-50%);
}

.select__list {
  width: fit-content;
  cursor: pointer;
  z-index: 1;
  background-color: rgba(249, 249, 249, .5);
  border: 1px solid #d1d1d1;
  border-top: none;
  border-radius: 0 0 12px 12px;
  font-size: 15px;
  font-weight: 700;
  line-height: 1.467;
  display: none;
  position: absolute;
  top: 100%;
  left: -17px;
  overflow: hidden;
  transform: translateY(10px);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12);
}

.select__list--visible {
  display: inline-block;
}

.select__option {
  background-color: #f9f9f9;
  border-bottom: 1px solid #d1d1d1;
  padding: 9px 16px;
}

.select__option:first-child {
  border-radius: 12px 12px 0 0;
}

.select__option:last-child {
  border-bottom: none;
  border-radius: 0 0 12px 12px;
}

.select__option:hover, .select__option:focus {
  background-color: #f4f8ec;
}

.controls__list {
  column-gap: 40px;
  display: flex;
}

.controls__link {
  min-width: 24px;
  min-height: 24px;
  display: inline-block;
}

.controls__icon {
  width: 20px;
  height: 18px;
  fill: none;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.controls__link:hover .controls__icon, .controls__link:hover .controls__icon, .controls__link:focus .controls__icon, .controls__link:focus .controls__icon {
  transform: scale(1.3);
}

.controls__item--bag {
  min-width: 30px;
  min-height: 30px;
  position: relative;
}

.controls__indicator {
  width: 16px;
  height: 16px;
  text-align: center;
  color: #fff;
  background-color: #e6704b;
  border-radius: 12px;
  padding: 1px 4px;
  font-size: 12px;
  transition: transform .5s ease-in-out;
  display: block;
  position: absolute;
  bottom: 3px;
  left: -6px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .15);
}

.controls__link:hover .controls__indicator, .controls__link:focus .controls__indicator {
  transform: rotate(360deg)scale(1.1);
}

.menu {
  background-color: #f9f9f9;
}

.menu__list {
  color: #151515;
  align-items: center;
  column-gap: 40px;
  padding-top: 16px;
  padding-bottom: 16px;
  font-size: 15px;
  font-weight: 500;
  line-height: 1.47;
  display: flex;
}

.menu__link {
  align-items: center;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
}

.menu__icon {
  width: 12px;
  height: 12px;
  transition: transform .3s linear;
}

.menu__link:hover .menu__icon, .menu__link:focus .menu__icon {
  transform: rotate(90deg)translateY(-50%);
}

.menu__link:hover, .menu__link:focus {
  text-shadow: 0 1px 3px rgba(70, 118, 10, .12);
  transform: scale(1.2);
}

.aside {
  flex-basis: 269px;
}

.aside__item {
  margin-bottom: 12px;
}

.aside__item:last-child {
  margin-bottom: 48px;
}

.aside__link {
  color: #6a983c;
  font-family: Open Sans;
  font-size: 14px;
  line-height: 1.36;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
}

.aside__link:hover, .aside__link:focus {
  text-shadow: 0 1px 3px rgba(70, 118, 10, .12);
  transform: scale(1.2);
}

.more-btn {
  background-color: #f5f5f5;
  border: none;
  border-radius: 12px;
  justify-content: space-between;
  align-items: center;
  padding: 12.5px 16px;
  font-size: 15px;
  font-weight: 700;
  line-height: 1.47;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
}

.more-btn__icon {
  width: 16px;
  height: 16px;
  margin-left: 6px;
  transition: transform .3s linear .1s;
}

.more-btn:hover .more-btn__icon, .more-btn:focus .more-btn__icon {
  transform: rotate(180deg);
}

.more-btn:hover, .more-btn:focus {
  transform: scale(1.1);
  box-shadow: 0 1px 3px rgba(70, 118, 10, .5);
}

.hero__container {
  justify-content: space-between;
  align-items: center;
  column-gap: 32px;
  padding-top: 64px;
  padding-bottom: 64px;
  display: flex;
}

.hero__card {
  max-height: 280px;
  background-color: #92c064;
  background-image: url("banner.62bef3e8.jpg");
