.read-btn {
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #92c064;
  border-radius: 12px;
  justify-content: space-between;
  align-items: center;
  padding: 12.5px 16px;
  font-size: 15px;
  font-weight: 700;
  line-height: 1.47;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
}

.read-btn__icon {
  width: 16px;
  height: 16px;
  margin-left: 6px;
  transition: transform .3s linear .1s;
}

.read-btn:hover .read-btn__icon, .read-btn:focus .read-btn__icon {
  transform: rotate(180deg);
}

.read-btn:hover, .read-btn:focus {
