}

.basicLightbox--visible {
  width: 100vw;
  height: 100%;
  opacity: 1;
  z-index: 100;
  background-color: rgba(0, 0, 0, .2);
  display: block;
  position: fixed;
  top: 0;
  left: 0;
}

.basicLightbox__placeholder {
  width: 568px;
  background-color: #f4f8ec;
  border: 1px solid #46760a;
  border-radius: 10px;
  padding: 32px 42px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.modal {
