.product {
  height: 100%;
  &__thumb {
    height: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    row-gap: 16px;
    justify-content: space-between;
  }

  &__link {
    display: block;
    flex-grow: 1;
  }

  // &__link:hover &__img-wrapper,
  // &__link:focus &__img-wrapper {
  //   transform: scale(1.1);
  // }

  &__img-wrapper {
    width: 237px;
    height: 180px;
    border-radius: 12px;
    // background-image: url('../images/image-light.jpg');
    background-color: $thumb-border-grey;
    position: relative;
    @include transition(transform);
  }
  &__image {
    border-radius: 12px;

    &:hover,
    &:focus {
      transform: scale(1.1);
    }
  }

  &__discount {
    position: absolute;
    top: 12px;
    left: 12px;
    max-width: 54px;
    max-height: 18px;
    padding: 0 8px;
    background-color: $discount-bg-green;
    border-radius: 12px;
    font-weight: 600;
    font-size: 12px;
    line-height: 1.5;
    color: $accent-green;
    z-index: 100;
  }

  &__title {
    font-weight: 500;
    font-size: 15px;
    line-height: 1.47;
    margin-bottom: 4px;
  }
  &__description {
    font-family: 'Open Sans';
    font-size: 12px;
    line-height: 1.33;
    color: $text-secondary;
  }

  &__price-tag {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__new-price {
    font-weight: 600;
    font-size: 18px;
    line-height: 1.5;
  }
  &__old-price {
    font-weight: 600;
    font-size: 12px;
    line-height: 1.5;
    text-decoration-line: line-through;
    color: $text-pale-grey;
  }
}
